import AddTask from "./Addtask";
import "./App.css";
import Header from "./Head";
import Content from "./Content";
import React, { useState, useEffect } from "react";
import Footer from './Footer';
import { BrowserRouter as Router, Route, Routes} from "react-router-dom";

function App() {
  const [contacts, setContacts] = useState([]);
const LOCAL_STORAGE_KEY = "contacts";
  const AddContactHandler = (contact) => {
    setContacts([...contacts, { ...contact }]);
  };

  useEffect(() => {
    const retriveContacts = JSON.parse(localStorage.getItem(LOCAL_STORAGE_KEY));
    if (retriveContacts) setContacts(retriveContacts);
  }, []);


  useEffect(() => {
    localStorage.setItem(LOCAL_STORAGE_KEY, JSON.stringify(contacts));
  }, [contacts]);

  const handelDelete = (id) => {
    const newTaskAdded = contacts.filter((contact) => {
      return contact.id !== id;
    });
    setContacts(newTaskAdded);
    console.log(contacts);
  };

  const showHim = () =>{
    return false;
  }
  return (
    <>
      <Header />
      <Router>
        <Routes>
          <Route path="/" element={<AddTask AddContactHandler={AddContactHandler} />}/>
          <Route path="/data" element={<Content data={contacts} handelDelete={handelDelete} />}/>
      
      </Routes>
      <hr/>
      <Content data={contacts} handelDelete={handelDelete} dontShow={showHim} />
      </Router>
      <Footer />
    </>
  );
  }

export default App;
