import React from "react";
// import { Link } from "react-router-dom";

const Content = (props) => {
  const handelDeleteNow = (id) => {
    props.handelDelete(id);
  };

  // <span class="badge bg-danger">High</span>

  const ShowMe = props.data.map((m) => {
    return (
      <div className="card mt-2" key={m.id}>
        <div className="card-header">
          {m.check === true ? (
            <span className="badge bg-danger">High</span>
          ) : (
            <span className="badge bg-warning">Low</span>
          )}
        </div>
        <div className="card-body">
          <h5 className="card-title">
            {m.topic}
            <button
              className="float-end"
              onClick={() => handelDeleteNow(m.id)}
              style={{ border: "none", background: "white" }}
            >
              <svg
                xmlns="http://www.w3.org/2000/svg"
                style={{ color: "red" }}
                width="20"
                height="20"
                fill="currentColor"
                className="bi bi-trash-fill"
                viewBox="0 0 16 16"
              >
                <path d="M2.5 1a1 1 0 0 0-1 1v1a1 1 0 0 0 1 1H3v9a2 2 0 0 0 2 2h6a2 2 0 0 0 2-2V4h.5a1 1 0 0 0 1-1V2a1 1 0 0 0-1-1H10a1 1 0 0 0-1-1H7a1 1 0 0 0-1 1H2.5zm3 4a.5.5 0 0 1 .5.5v7a.5.5 0 0 1-1 0v-7a.5.5 0 0 1 .5-.5zM8 5a.5.5 0 0 1 .5.5v7a.5.5 0 0 1-1 0v-7A.5.5 0 0 1 8 5zm3 .5v7a.5.5 0 0 1-1 0v-7a.5.5 0 0 1 1 0z" />
              </svg>
            </button>
          </h5>
          <p className="card-text">{m.desc}</p>
        </div>
      </div>
    );
  });

//   const renderMine = (props) =>{
//     if(props.dataShow){
//     return(
//   <Link to="/">
//   <svg
//     style={{
//       bottom: "20",
//       right: "20",
//       position: "fixed"
//     }}
//     className="float-end text-warning"
//     xmlns="http://www.w3.org/2000/svg"
//     width="60"
//     height="60"
//     fill="currentColor"
//     className="bi bi-plus-circle-fill"
//     viewBox="0 0 16 16"
//   >
//     <path d="M16 8A8 8 0 1 1 0 8a8 8 0 0 1 16 0zM8.5 4.5a.5.5 0 0 0-1 0v3h-3a.5.5 0 0 0 0 1h3v3a.5.5 0 0 0 1 0v-3h3a.5.5 0 0 0 0-1h-3v-3z" />
//   </svg>
// </Link>
//     );}
// }

  return (
    <>
      <div className="container">
        {props.data.length === 0 ? <h2>No Task Added Yet </h2> : <h2>Tasks</h2>}
        {ShowMe}
      </div>
    </>
  );
};

export default Content;
